import { Component, h, Prop } from '@stencil/core';
import { IDocument } from '../../types/Document';

@Component({
    tag: 'document-validate-info',
    shadow: true,
    styleUrl: 'document-validate-info.scss',
})
export class DocumentValidateInfo {
    @Prop() document: IDocument;

    constructor() {
        this.hasProperty = this.hasProperty.bind(this);
    }

    public render() {
        return [
            <div class="validate-document-success">
                <div class="validate-document-summary">
                    <h3>Summary</h3>
                    <ul class="validate-document-property-list">
                        <li>
                            <span class="document-property">Name: </span>
                            <span>{this.document.name}</span>
                        </li>
                        <li>
                            <span class="document-property">Value: </span>
                            <span>{this.document.value}</span>
                        </li>
                        <li>
                            <span class="document-property">
                                Document is for signing:{' '}
                            </span>
                            <span>
                                {this.hasProperty(this.document.is_signing)}
                            </span>
                        </li>
                        <li>
                            <span class="document-property">
                                Video is added:{' '}
                            </span>
                            <span>
                                {this.hasProperty(this.document.is_video)}
                            </span>
                        </li>
                        <li>
                            <span class="document-property">
                                Send smart reminders:{' '}
                            </span>
                            <span>
                                {this.hasProperty(
                                    this.document.is_reminder_sending
                                )}
                            </span>
                        </li>
                        <li>
                            <span class="document-property">
                                Send link by SMS:{' '}
                            </span>
                            <span>
                                {this.hasProperty(this.document.is_sms_sending)}
                            </span>
                        </li>
                    </ul>
                </div>

                <div class="validate-document-recipients">
                    <h3>Recipients</h3>
                    <ul class="document-recipient-list">
                        {this.document.recipients.map(recipient => {
                            return (
                                <recipient-item
                                    recipient={recipient}
                                    showAdd={false}
                                />
                            );
                        })}
                    </ul>
                </div>
            </div>,
        ];
    }

    hasProperty(value): string {
        return value ? 'Yes' : 'No';
    }
}
