import { Component, h, Prop } from '@stencil/core';
import { IError } from '../../types/Error';
import { IDocument } from '../../types/Document';

@Component({
    tag: 'document-error-feedback',
    shadow: true,
    styleUrl: 'document-error-feedback.scss',
})
export class DocumentErrorFeedback {
    @Prop() document: IDocument;
    @Prop() errorList: IError[] = [];

    public render() {
        return [
            <div>
                <h3>You need to fix following tasks to send:</h3>
                <ul class="document-error-list">
                    {this.errorList.map(error => {
                        return <document-error error={error} />;
                    })}
                </ul>
            </div>,
        ];
    }
}
