@import '../../gacolors.scss';

.document-error {
    display: flex;
    align-items: center;
    padding: 1rem 1rem;
    cursor: pointer;

    .document-error-icon {
        display: inherit;
        padding: 0.5rem;
        margin-right: 1.5rem;
        border-radius: 50%;
        background-color: $ga-red;
        color: $ga-white;
    }

    .document-error-info {
        display: flex;
        flex-direction: column;

        .document-error-header {
            margin-block-end: 0;
            margin-block-start: 0;
        }
    }

    &:hover {
        background-color: $ga-lighter-gray;
    }

    @media (min-width: 1074px) {
        width: 50%;
    }

    @media (max-width: 1075px) {
        width: 100%;
    }
}
