@import '../../gacolors.scss';

.share-document-list-item {
    margin: 0.5rem 0;
    padding: 0.5rem 1rem;

    .recipient-info-container {
        display: flex;
        align-items: center;
        padding-left: 1rem;

        .recipient-icon {
            display: flex;
            align-items: center;
            margin-right: 1rem;
            padding: 0.5em;
            border-radius: 50%;
            background-color: $ga-blue;
            color: $ga-white;
        }

        .recipient-info {
            display: flex;
            flex-direction: column;

            .recipient-info-name {
                text-transform: capitalize;
                font-weight: bold;
            }

            .recipient-info {
                text-transform: capitalize;
            }
        }
    }

}
