import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ga-loader-with-text',
    shadow: true,
    styleUrl: 'ga-loader-with-text.scss',
})
export class GALoaderWithText {
    @Prop() public showText: boolean = false;
    @Prop() public text: string;

    public render() {
        return (
            <div class="share-document-loading-container">
                {(() => {
                    if (this.showText) {
                        return (
                            <div>
                                <h3>{this.text}</h3>
                            </div>
                        );
                    }
                })()}
                <ga-loader />
            </div>
        );
    }
}
