import { Component, h, Prop, EventEmitter, Event } from '@stencil/core';
import { IError } from '../../types/Error';
import { EnumViews } from '../../models/EnumViews';

@Component({
    tag: 'document-error',
    shadow: true,
    styleUrl: 'document-error.scss',
})
export class DocumentError {
    @Prop() error: IError;

    @Event() changeView: EventEmitter<EnumViews>;

    constructor() {
        this.onClick = this.onClick.bind(this);
    }

    public render() {
        return [
            <li class="document-error" onClick={this.onClick}>
                <div class="document-error-icon">
                    <limel-icon name={this.error.icon} size="small" />
                </div>
                <div class="document-error-info">
                    <h4 class="document-error-header">{this.error.header}</h4>
                    <span>{this.error.title}</span>
                </div>
            </li>,
        ];
    }

    private onClick() {
        this.changeView.emit(this.error.view);
    }
}
